import React, { useState } from 'react'
import styled from 'styled-components'
import logo from '../Images/logo.png'
import {Link} from 'react-router-dom'
import home from '../Images/home2.png'
import {HamburgerIcon,CloseIcon} from '@chakra-ui/icons'
import AnchorLink from "react-anchor-link-smooth-scroll";


const Navbar = ({viewResume}) => {
  const [open,close]=useState(false)

  function myFunction(){
    !open?close(true):close(false)
  }

  return (
    <>
    <DIV id='nav-menu'>
        <div>
            <img src={home}/>
            <h1>{'</> Anubhav'} </h1>
        </div>
        <div>
            <AnchorLink href='#home' className="nav-link home" style={{textDecoration:'none'}}><p>Home</p></AnchorLink>
            <AnchorLink href='#about' className="nav-link about" style={{textDecoration:'none'}}><p>About</p></AnchorLink>
            <AnchorLink href='#skills' className="nav-link skills" style={{textDecoration:'none'}}><p>Skills</p></AnchorLink>
            <AnchorLink href='#projects' className="nav-link projects" style={{textDecoration:'none'}}><p>Projects</p></AnchorLink>
            <AnchorLink href='#contact' className="nav-link contact" style={{textDecoration:'none'}}><p>Contact</p></AnchorLink>
            <button id="resume-button-1" className="nav-link resume" onClick={viewResume}>Resume</button>
            
            <p className='icon' onClick={myFunction}>{open?<CloseIcon/>:<HamburgerIcon/>}</p>         
        </div>
    </DIV>
    {open && <Ham>
            <AnchorLink href='#home' className="nav-link home" style={{textDecoration:'none'}}><p>Home</p></AnchorLink>
            <AnchorLink href='#about' className="nav-link about" style={{textDecoration:'none'}}><p>About</p></AnchorLink>
            <AnchorLink href='#skills' className="nav-link skills" style={{textDecoration:'none'}}><p>Skills</p></AnchorLink>
            <AnchorLink href='#projects' className="nav-link projects" style={{textDecoration:'none'}}><p>Projects</p></AnchorLink>
            <AnchorLink href='#contact' className="nav-link contact" style={{textDecoration:'none'}}><p>Contact</p></AnchorLink>
            <AnchorLink href='#resume-button-1' className="nav-link resume" style={{textDecoration:'none'}}><p>Resume</p></AnchorLink>
    </Ham>}
    </>
  )
}

export default Navbar

const Ham=styled.div`
    background-color:#000000;
    color:#b5af41;
    font-size: 150%;
    padding: 100px;
    position: fixed;
    height: 1200px;
    width: 510px;
    float: right;
    

    @media screen and (min-width: 990px){
        display: none;
    }

    @media screen and (max-width: 700px){
        width: 320px;
    }
    p{
        color: #b5af41;
    }
`

const DIV=styled.div`
    width:100%;
    height:100px;
    background-color:#000000;
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    /* box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px; */
    
    *{
        margin: 0;padding:0;
    }
    

    div:first-child{
        display: flex;
        color:#b5af41;
        font-size: 140%;
        align-items: center;
        /* justify-content: space-between; */
        width: 50%;
    }
    div:first-child h1{
       margin: 0 0 0 -3%;
    }

    div img{
        width: 155px;
        height: 140px;
    }

    div:last-child{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 50%;
        font-size: 20px;
    }
    div:last-child p{
        color: #b5af41;
    }
    div:last-child button{
        background-color: #b5af41;
        color: #000000;
        border: none;
        border-radius: 10px;
        padding: 10px 15px;
        font-size: 20px;
    }

    .icon{
        display: none;
    }

    @media screen and (max-width: 990px) and (min-width: 700px) {
        .nav-link:not(.resume) {display: none;}
        .icon {
            /* float: right; */
            display: block;
        }
        div:first-child{
            width: 70%;
        }
        div:last-child{
            width: 30%;
        }

    }

    @media screen and (max-width: 700px) and (min-width: 200px) {
        .nav-link {display: none;}
        
        .icon {
            float: none;
            display: block;
        }
        
        div:last-child{
            width: 20%;
        }
        div:last-child button{
            padding: 7px 9px;
            font-size: 15px;

        }
        div:first-child{
            font-size:15px;
            width: 80%;
        }
        div:first-child h1{
            /* margin: 0 0 0 -6%; */
        }
        div img{
            width: 130px;
            height: 90px;
            margin: 0 -10px;
        }
    }
    
`