import React from 'react'
import styled from 'styled-components'
import contact from '../Images/contact.png'
import {PhoneIcon} from '@chakra-ui/icons'
import email from '../Images/email.png'
import phone from '../Images/phone.png'
import git from '../Images/git.png'
import linkedin from '../Images/linkedin.png'

const Contact = () => {
  return (
    <DIV id='contact'>
      <img src={contact} className='cont' alt="" />
      <div>
        <h1>
          CONTACT 
        </h1>
        <div>
          <img src={email} alt=""/>
          <h2>anubhavdixit268@gmail.com</h2>
        </div>
        <div>
          <img src={phone} alt="" />
          <h2>+91 6392185444</h2>
        </div>
        <div>
          <img src={linkedin} alt="" />
          <h2>anubhav-dixit</h2>
        </div>
        <div>
          <img src={git} alt="" />
          <h2>Anubhav0391</h2>
        </div>
        
      </div>
    </DIV>
  )
}

export default Contact

const DIV =styled.div`
    /* height: ; */
    color:#b5af41;
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding-bottom: 20px;
    *{
      margin: 0;padding:0
    }
    h1{
        font-size: 80px;
        color: #2c4e8a;
        border-bottom: 5px solid #2c4e8a;
        width: fit-content;
        margin: 30px 0;
    }
    h1,h2,h3{
        
       
    }
    h2{
        font-size: 35px;    
        width:270px ;
        margin: 30px 0;
    }
    .cont{
      width: 40%;
      height: 500px;
      padding: 50px 75px 30px ;
      /* border: 1px solid white; */
    }

    div{
      width: 50%;
    }
    div div{
      display: flex;
      align-items: center;
      justify-content: space-between;
      /* margin-left: 30px; */
    }
    div img{
      width: 80px;
    }
`