import React from 'react'
import styled from 'styled-components'
import home from '../Images/home2.png'
import logo from '../Images/logo.png'

const Home = ({viewResume}) => {
  return (
    <DIV id='home'>
        <div>
            <h1>HEY !</h1>
            <h2>I am ...</h2>
            <h3>... an aspiring Full Stack Developer at Masai School.</h3>
            <button id="resume-button-2" onClick={viewResume}>RESUME</button>
        </div>
        <div>
           <img src={logo}/>
        </div>
    </DIV>
  )
}

export default Home

const DIV =styled.div`
    /* background-color: #042820;
    background-color: #000000; */
    height: 60vh;
    color:#b5af41;
    display: flex;
    justify-content: space-between;
    padding: 110px 0;
    *{
        margin: 0;
        padding: 0;
    }

    h1{
        font-size: 80px;
        /* color: #860606; */
    }
    h2{
        font-size: 60px;
        color: #a91005;
    }
    h3{
        font-size: 45px;
        
    }
    h1,h2,h2,button{
        margin: 40px 0;
    }
    button{
        background-color: #860606;
        color: #b5af41;
        border: none;
        border-radius: 50px;
        padding: 15px 35px;
        font-size: 40px;
    }
    div:first-child{
        text-align: left;
        padding: 0px 100px 50px;
    
    }
    div{
        width: 45%;
        height:100%;
    }
    img{
        text-align: right;
        width: 100%;
        height: 120%;
        /* width:100%; */
        /* border:1px solid white; */
        /* border-top-left-radius: 190px; */
    }

    @media screen and (max-width: 990px) and (min-width:400px ){
        flex-direction: column;
        align-items: center;
        div{
            width: 100%;
        } 
         div:first-child{
            margin: 0 50px;
        }
    }
`