import React from 'react'
import styled from 'styled-components'
import git from '../Images/git.svg'
import css from '../Images/css3.svg'
import html from '../Images/html-5.svg'
import javascript from '../Images/javascript.svg'
import mongodb from '../Images/mongodb.svg'
import node from '../Images/nodejs.svg'
import react from '../Images/reactjs.svg'
import redux from '../Images/redux.svg'


const Skills = () => {
  return (
    <DIV id='skills'>
      <h1>SKILLS</h1>
      <grid>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={html} alt={'html'} />
            <h2 className="skills-card-name">HTML</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={css} alt={'css'} />
            <h2 className="skills-card-name">CSS</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={javascript} alt={'js'} />
            <h2 className="skills-card-name">JavaScript</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={react} alt={'react'} />
            <h2 className="skills-card-name">ReactJS</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={redux} alt={'redux'} />
            <h2 className="skills-card-name">ReduxJS</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={node} alt={'node'} />
            <h2 className="skills-card-name">NodeJs</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={mongodb} alt={'mongodb'} />
            <h2 className="skills-card-name">MongoDB</h2>
          </div>
        </div>
        <div className="skills-card">
          <div>
            <img className="skills-card-img" src={git} alt={'git'} />
            <h2 className="skills-card-name">Git</h2>
          </div>
        </div>
      </grid>  
    </DIV>
  )
}

export default Skills

const DIV =styled.div`
    height: 100vh;
    padding: 100px;

    grid{
      display: grid;
      /* position:-webkit-sticky; */
      grid-template-columns: repeat(4,1fr);
      padding-left:120px;
    }

    *{
      margin: 0;
      padding:0;
    }

    h1{
        font-size: 80px;
        color: #938e23;
        width: fit-content;
        margin: auto;
        border-bottom:5px solid #938e23;
        margin-bottom:10px
    }
    h2{
      font-size: 24px;
      color: #48450e;display:none
    }
    
    grid>div{
      width: 150px;
      height: 150px;
      background: linear-gradient(#636363, #a2ab58);
      padding: 25px;
      /* rotate: 45deg; */
      border-radius: 35%;
      margin-top: 60px;
      cursor: pointer;
    }

    grid>div>div{
      /* rotate:-45deg */
    }
    
    div img{
      width: 100%;
      height: 150px;
    }
    grid>div:hover{
      rotate: 45deg;
      img{
        height: 100%;
        width: 80%;
      }
      div{
        width:101%;
        overflow: hidden;
        rotate: -45deg;
      h2{
        /* font-size: 26px; */display:block
        /* padding: 6px; */
      }
      }
    }
`