
import './App.css';
import About from './components.jsx/About';
import Contact from './components.jsx/Contact';
import Github from './components.jsx/Github';
import Home from './components.jsx/Home';
import Navbar from './components.jsx/Navbar';
import Projects from './components.jsx/Project';
import Skills from './components.jsx/Skills';

function App() {
  const viewResume = () => {
    window.location='https://drive.google.com/file/d/1Zp0P8UGwoU05-UBUWMtFxLUxpY2P7_ap/view?usp=sharing'

    fetch('Anubhav_Dixit_Resume.pdf').then(response => {
        response.blob().then(blob => {
            const fileURL = window.URL.createObjectURL(blob);
            let alink = document.createElement('a');
            alink.href = fileURL;
            alink.download = 'Anubhav_Dixit_Resume.pdf';
            alink.click();
        })
    })
  }

  return (
    <div className="App">
      <Navbar viewResume={viewResume}/>
      <div style={{marginTop:'100px'}}>
        <Home viewResume={viewResume}/>
        <About/>
        <Skills/>
        <Projects/>
        <Github/>
        <Contact/>
      </div>
    </div>
  );
}

export default App;
