import React from 'react'
import styled from 'styled-components'
import ad from '../Images/ad.jpg'

const About = () => {
  return (
    <DIV id='about' className='about section'>
      <img className="home-img" src={ad} alt='Profile Image' />
      <div>
        <h1>ABOUT ME</h1>
        <h2>I'm <span id="user-detail-name">Anubhav Dixit</span> ,</h2>
        <h3 id="user-detail-intro">Passionate Full Stack Web Developer with robust design and
        problem-solving skills. A highly disciplined and motivated
        person with proficiency in various front-end and back-end
        skills like MongoDB, ExpressJS, ReactJS, NodeJS, etc.
        Seeking to leverage skill sets and expertise in an exciting
        tech company.
        </h3>
      </div>
    </DIV>
  )
}

export default About

const DIV =styled.div`
    height: 100vh;
    color:#b5af41;
    padding: 50px 80px 30px 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: justify;
    
    h1{
        font-size: 80px;
        color: #938e23;
        border-bottom: 5px solid #938e23;
        width: fit-content;
    }
    h2{
        font-size: 60px;
        color: #9f0e0e;
    }
    h3{
        font-size: 30px;
        color: #938e23;
    }
    h1,h2,h2{
        margin: 40px 0;
    }
    img{
      width: 40%;
      border-radius: 50%;
      border: 5px solid #938e23;
    }
    div{
      width: 55%;
    }
`